/* src/components/chat/CreateGroupChatModal.css */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background: white;
    border-radius: 15px;
    width: 90%;
    max-width: 500px;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
}

.modal-header {
    padding: 1rem;
    border-bottom: 1px solid #e9ecef;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h2 {
    margin: 0;
    font-size: 1.25rem;
}

.close-btn {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}

.modal-body {
    padding: 1rem;
    overflow-y: auto;
}

.friend-list {
    max-height: 300px;
    overflow-y: auto;
}

.friend-item {
    display: flex;
    align-items: center;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s;
}

.friend-item:hover {
    background-color: #f8f9fa;
}

.friend-item.selected {
    background-color: #e3f2fd;
}

.friend-info {
    margin-left: 10px;
}

.friend-name {
    font-size: 1rem;
    color: #333;
}

.group-name-input {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.group-name-input input {
    padding: 10px;
    border: 1px solid #ced4da;
    border-radius: 8px;
    font-size: 1rem;
}

.modal-footer {
    padding: 1rem;
    border-top: 1px solid #e9ecef;
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

.btn {
    padding: 8px 20px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    font-size: 1rem;
}

.btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

.cancel {
    background-color: #6c757d;
    color: white;
}

.next, .create {
    background-color: #007bff;
    color: white;
}

.back {
    background-color: #6c757d;
    color: white;
}