.registerContainer {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ddd;
}

.registerBox {
    width: 100%;
    max-width: 400px;
    padding: 2rem;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.registerTitle {
    text-align: center;
    color: #333;
    margin-bottom: 2rem;
    font-size: 2rem;
}

.registerForm {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.inputGroup {
    margin-bottom: 1rem;
}

.inputLabel {
    display: block;
    margin-bottom: 0.5rem;
    color: #666;
}

.inputField {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1rem;
    transition: border-color 0.3s;
}

.inputField:focus {
    outline: none;
    border-color: #4CAF50;
}

.registerButton {
    background-color: #4CAF50;
    color: white;
    padding: 0.75rem;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 1rem;
}

.registerButton:hover {
    background-color: #45a049;
}

.loginLink {
    text-align: center;
    margin-top: 1rem;
}

.loginLink a {
    color: #4CAF50;
    text-decoration: none;
    font-size: 0.9rem;
}

.loginLink a:hover {
    text-decoration: underline;
}

.errorMessage {
    color: #ff4444;
    font-size: 0.9rem;
    margin-top: 0.5rem;
    text-align: center;
} 