.loginContainer {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
}

.loginBox {
    width: 100%;
    max-width: 400px;
    padding: 2rem;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.loginTitle {
    text-align: center;
    color: #333;
    margin-bottom: 2rem;
    font-size: 2rem;
}

.loginForm {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.inputGroup {
    margin-bottom: 1rem;
}

.inputLabel {
    display: block;
    margin-bottom: 0.5rem;
    color: #666;
}

.inputField {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1rem;
    transition: border-color 0.3s;
}

.inputField:focus {
    outline: none;
    border-color: #4CAF50;
}

.loginButton {
    background-color: #4CAF50;
    color: white;
    padding: 0.75rem;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 1rem;
}

.loginButton:hover {
    background-color: #45a049;
}

.registerLink {
    text-align: center;
    margin-top: 1rem;
}

.registerLink a {
    color: #4CAF50;
    text-decoration: none;
    font-size: 0.9rem;
}

.registerLink a:hover {
    text-decoration: underline;
} 