/* modal.css */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background: white;
    padding: 2rem;
    border-radius: 12px;
    width: 90%;
    max-width: 500px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* ÌÉ≠ ÎîîÏûêÏù∏ */
.tab_wrap {
    display: flex;
    gap: 1rem;
    margin-bottom: 1.5rem;
    border-bottom: 2px solid #eee;
    padding-bottom: 0.5rem;
}

.tab_btn {
    padding: 0.5rem 1rem;
    cursor: pointer;
    font-weight: 500;
    color: #666;
    transition: all 0.3s ease;
    caret-color: transparent;
}

.tab_btn.active {
    color: #4a90e2;
    border-bottom: 2px solid #4a90e2;
    margin-bottom: -0.5rem;
}

/* Í≤ÄÏÉâ ÏòÅÏó≠ */
.cont_search form {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
    flex-wrap: wrap;
}

.cont_search input {
    flex: 1;
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 1rem;
}

.cont_search button {
    padding: 0.8rem 1.5rem;
    background: #4a90e2;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.cont_search button:hover {
    background: #357abd;
}

/* Í≤ÄÏÉâ Í≤∞Í≥º Î™©Î°ù */
.search-results {
    width: 100%;
    margin-top: 1rem;
    max-height: 300px;
    overflow-y: auto;
}

.user-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border: 1px solid #eee;
    margin-bottom: 10px;
}

.user-item:last-child {
    margin-bottom: 0;
}

/* ÏπúÍµ¨ ÏöîÏ≤≠ Î™©Î°ù */
.friend-requests {
    margin-top: 1rem;
    max-height: 300px;
    overflow-y: auto;
}

.request-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border-bottom: 1px solid #eee;
}

.request-actions {
    display: flex;
    gap: 0.5rem;
}

.request-actions button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.request-actions button:first-child {
    background: #4a90e2;
    color: white;
}

.request-actions button:last-child {
    background: #e74c3c;
    color: white;
}

.request-actions button:hover {
    opacity: 0.9;
}

/* Î°úÎî© ÏÉÅÌÉú */
.searching {
    text-align: center;
    padding: 1rem;
    color: #666;
}

/* Îπà ÏÉÅÌÉú Î©îÏãúÏßÄ */
.no-requests {
    text-align: center;
    padding: 2rem;
    color: #666;
    font-style: italic;
}

/* Îã´Í∏∞ Î≤ÑÌäº */
.modal-content > button:last-child {
    margin-top: 1.5rem;
    padding: 0.8rem 1.5rem;
    background: #f5f5f5;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    width: 100%;
    transition: background 0.3s ease;
}

.modal-content > button:last-child:hover {
    background: #e0e0e0;
}

/* Ïä§ÌÅ¨Î°§Î∞î Ïä§ÌÉÄÏùºÎßÅ */
.search-results::-webkit-scrollbar,
.friend-requests::-webkit-scrollbar {
    width: 6px;
}

.search-results::-webkit-scrollbar-track,
.friend-requests::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.search-results::-webkit-scrollbar-thumb,
.friend-requests::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 3px;
}

.search-results::-webkit-scrollbar-thumb:hover,
.friend-requests::-webkit-scrollbar-thumb:hover {
    background: #555;
}